/**  */
export const isClient = typeof window !== 'undefined'
/**  IOS */
export const isIOS =
  /* #__PURE__ */ isClient &&
  window?.navigator?.userAgent &&
  /iP(ad|hone|od)/.test(window.navigator.userAgent)

const toString = Object.prototype.toString
const hasOwnProperty = Object.prototype.hasOwnProperty

/**
 * 
 *
 * @param value 
 * @param type 
 *
 * @returns 
 */
export function is(value: unknown, type: string) {
  return toString.call(value) === `[object ${type}]`
}

/**
 * 
 *
 * @param value 
 * @param key 
 *
 * @returns 
 */
export function has(value: Record<string, any>, key: string | symbol): key is keyof typeof value {
  return hasOwnProperty.call(value, key)
}

/**
 * 
 *
 * @param value 
 *
 * @returns 
 */
export function isDefined<T = unknown>(
  value: T | undefined | null
): value is Exclude<T, undefined | null> {
  return value !== undefined && value !== null
}

/**
 * 
 *
 * @param value 
 *
 * @returns 
 */
export function isNull(value: unknown): value is null | undefined {
  return value === undefined || value === null
}

/**
 * 
 *
 * @param value 
 *
 * @returns 
 */
export function isNumber(value: unknown): value is number {
  return typeof value === 'number'
}

/**
 *  `NaN`
 *
 * @param value 
 *
 * @returns  `NaN`
 */
export function isNaN(value: unknown): value is number {
  return Number.isNaN(value)
}

/**
 * 
 *
 * @param value 
 *
 * @returns 
 */
export function isString(value: unknown): value is string {
  return typeof value === 'string'
}

/**
 * 
 *
 * @param value 
 *
 * @returns 
 */
export function isBoolean(value: unknown): value is boolean {
  return typeof value === 'boolean'
}

/**
 *  `true`
 *
 * @param value 
 *
 * @returns  `true`
 */
export function isTrue(value: unknown): value is true {
  return value === true
}

/**
 *  `false`
 *
 * @param value 
 *
 * @returns  `false`
 */
export function isFalse(value: unknown): value is false {
  return value === false
}

/**
 *  `Symbol`
 *
 * @param value 
 *
 * @returns  `Symbol`
 */
export function isSymbol(value: unknown): value is symbol {
  return typeof value === 'symbol'
}

/**
 *  `BigInt`
 *
 * @param value 
 *
 * @returns  `BigInt`
 */
export function isBigInt(value: unknown): value is bigint {
  return typeof value === 'bigint'
}

/**
 * 
 *
 * @param value 
 *
 * @returns 
 */
export function isArray<T = any>(value: unknown): value is T[] {
  return Array.isArray(value)
}

/**
 * 
 *
 * `null` 
 *
 * @param value 
 *
 * @returns 
 */
export function isObject<T extends Record<any, any> = Record<any, any>>(
  value: unknown
): value is T {
  return is(value, 'Object')
}

/**
 *  `Promise`
 *
 *  `then`  `catch`  `Promise`
 *
 * @param value 
 *
 * @returns  `Promise`
 */
export function isPromise<T = any>(value: unknown): value is Promise<T> {
  return (
    !!value &&
    typeof (value as any).then === 'function' &&
    typeof (value as any).catch === 'function'
  )
}

/**
 * 
 *
 * @param value 
 *
 * @returns 
 */
export function isFunction(value: unknown): value is (...any: any[]) => any {
  return typeof value === 'function'
}

/**
 *  `Set`
 *
 * @param value 
 *
 * @returns  `Set`
 */
export function isSet<T = any>(value: unknown): value is Set<T> {
  return is(value, 'Set')
}

/**
 *  `Map`
 *
 * @param value 
 *
 * @returns  `Map`
 */
export function isMap<K = any, V = any>(value: unknown): value is Map<K, V> {
  return is(value, 'Map')
}

/**
 *  `Date`
 *
 * @param value 
 *
 * @returns  `Date`
 */
export function isDate(value: unknown): value is Date {
  return is(value, 'Date')
}

/**
 * 
 *
 * @param value 
 *
 * @returns 
 */
export function isRegExp(value: unknown): value is RegExp {
  return is(value, 'RegExp')
}

/**
 * 
 *
 * -  `length`  `0` 
 * -  `Set`  `Map` `size`  `0` 
 * - 
 * -  `NaN` 
 * - 
 *
 * @param value 
 *
 * @returns 
 */
export function isEmpty(value: unknown) {
  if (Array.isArray(value) || typeof value === 'string') {
    return value.length === 0
  }

  if (value instanceof Map || value instanceof Set) {
    return value.size === 0
  }

  if (isObject(value)) {
    return Object.keys(value).length === 0
  }

  if (typeof value === 'number') {
    return isNaN(value)
  }

  return isNull(value)
}

/**
 *  `Element`
 *
 * @param value 
 * @param ssr 
 *
 * @returns  `Element`
 */
export function isElement<T extends Element = Element>(value: unknown, ssr = false): value is T {
  if (!ssr && !isClient) return false

  return !!(value && 'nodeType' in (value as any))
}

/**
 * 
 *
 * @param value 
 *
 * @returns 
 */
export function isIterable(value: unknown) {
  return isDefined(value) && typeof (value as any)[Symbol.iterator] === 'function'
}

